<script>
	import { onMount } from 'svelte'
	import { gameWidth, gameHeight, volumeCoefficient, sensitivity as sensitivityStore, score, level, blocks, pixelRatio, canShare, shareMyScore, gameLang, gameOverTrack, version }
		from './props.js'

	import { lang as Translator } from './lang.js'

	import Timer from './Timer.js'
	import { Player } from './Player.js'
	import { Text } from './Text.js'
	import { Block } from './Block.js'

	// export let width
	// export let height
	let width
	let height
	
	let canvas
	let startScreen
	let crashScreen
	let startButton
	let restartButton
	$: sensitivity = $sensitivityStore
	let meter
	let audioContext
	let mediaStreamSource
	let recycle
	let lastBlockDeployedAt

	$: lang = (a,b,c,d,e,f,g) => {
		// we'll use $game lang here to update
		$gameLang;
		
		// the original translator
		return Translator(a,b,c,d,e,f,g)
	}

	let crashScreenScoreText =  'Come on! Try again'

	let mount = () => {
		// create canvas context
		let context = canvas.getContext('2d')

		// update gameWidth and gameHeight
		pixelRatio.set(window.devicePixelRatio)
		gameWidth.set(width)
		gameHeight.set(height)

		let versionText = new Text(context, 10, height-15, lang('version_text', {'version': $version}) )
		let volumeText 	= new Text(context, 10, 20, lang('volume_text', {'volume': 0}) )
		let scoreText 	= new Text(context, 100, 20, lang('score_text', {'score': $score}))
		let levelText 	= new Text(context, 170, 20, lang('level_text', {'level': $level}))
		
		let backgroundImage = new Image(); 
		backgroundImage.src = './dirtbg.png';
		
		let dirtImage = new Image(); 
		dirtImage.src = './dirt.png';
		
		// Push a player
		let player  	= new Player(context, dirtImage, width / 10, height / 2)
		
		blocks.set([
			// new Block(context, $level)
		])

		// we'll deploy new block
		let deploymentGap = (4000 - (($level-1) * 250));
		// console.log(deploymentGap, (Date.now()-lastBlockDeployedAt))
		// console.log(Date.now() - lastBlockDeployedAt)
		setInterval(() => {
			if( !lastBlockDeployedAt || (Date.now() - lastBlockDeployedAt) > deploymentGap  )
			{
				blocks.update(v => {
					let _block = new Block(context, $level)
					v.push( _block )
					return v
				})

				lastBlockDeployedAt = Date.now()
			}
		}, 100)

		// Animate-recycle
		recycle = new Timer(async function(){

			// update gameWidth and gameHeight
			pixelRatio.set(window.devicePixelRatio)
			gameWidth.set(width / 1)
			gameHeight.set(height / 1)
			// gameWidth.set(width / ($pixelRatio))
			// gameHeight.set(height / ($pixelRatio))

			// let volume = (meter ? meter.volume : 0) * $volumeCoefficient * ((($sensitivityStore+100.1) / 90)),
			let meterVolume = (meter ? meter.volume : 0) * $volumeCoefficient,
				// sensitivityCoefficient = (sensitivity / 10),
				sensitivityCoefficient = 1 + (sensitivity / 250),
				volume = Math.min(25, meterVolume) * sensitivityCoefficient,
				volumeMeter = volume.toFixed(1)
			
			// Clear the canvas
			context.clearRect(0, 0, width, height)
			await drawImg(context, backgroundImage);

			// Block
			let _blocks = [];
			blocks.subscribe(v => { _blocks = v })
			_blocks.forEach(block => {
				block.update(player)
			})

			// Update & draw the player
			player.update(volume)

			// 
			// volumeText.update(`Volume: ${volumeMeter}`)
			// scoreText.update(`Score: ${$score}`)
			// levelText.update(`Level: ${$level}`)

			volumeText.update( lang('volume_text', {'volume': volumeMeter})  );
			scoreText.update( lang('score_text', {'score': $score}) );
			levelText.update( lang('level_text', {'level': $level}) );
			versionText.update( lang('version_text', {'version': $version}) );

			// Did it crash?
			crashScreenScoreText = getCrashScreenScoreText()

			if( player.crashed )
			{
				gameOverTrack().play()
				crashScreen.style.display = 'flex'
				recycle.stop()
			}
		}, 1000 / 60)
	};
	
	function setGameLang(to)
	{
		to = to == 'tr' ? 'tr' : 'en';
 		gameLang.set(to)

 		// hide the lang changers
 		// document.querySelectorAll('.lang-changer').forEach(e => {
			// if( e.classList.contains('lang-changer-' + to) )
			// 	e.style.display = 'none'
			// else
			// 	e.style.display = 'inline-block'
 		// })
	}
	function startGame()
	{
		// Initiate mounting
		mount()

		// remove the start button
		startScreen.style.display = 'none'
		audioContext.resume()
	}
	function restartGame()
	{
		// 
		crashScreen.style.display = 'none'
		
		// update the score
		score.set(0)

		mount()
	}
	function handleDomReady() {
		// create audio context
		window.AudioContext = window.AudioContext || window.webkitAudioContext;

		// grab an audio context
		audioContext = new AudioContext();
		getAudio()
		handleGameSizes()
	}

	function handleDomResize()
	{
		handleGameSizes()
	}

	function handleSensitivity()
	{
		sensitivity = Math.max(-100, Math.min(100, sensitivity))
		sensitivityStore.set(sensitivity)
	}

	function handleGameSizes()
	{
		// ratio 8:1
		width = window.innerWidth / 1.3
		height = width / 2.5
	}

	function getAudio() {
		try {
			// monkeypatch getUserMedia
			// navigator.getUserMedia = 
			// 	navigator.getUserMedia ||
			// 	navigator.webkitGetUserMedia ||
			// 	navigator.mozGetUserMedia;

			navigator.mediaDevices.getUserMedia({
				"audio": {
					"mandatory": {
						"googEchoCancellation": "false",
						"googAutoGainControl": "false",
						"googNoiseSuppression": "false",
						"googHighpassFilter": "false"
					},
					"optional": []
				}
			}).then(gotStream, () => { alert('We can\'t reach your microphone') });

			// ask for an audio input
			// navigator.getUserMedia = ( navigator.getUserMedia ||
	 //                     navigator.webkitGetUserMedia ||
	 //                     navigator.mozGetUserMedia ||
	 //                     navigator.msGetUserMedia);
			// navigator.getUserMedia(
			// {
			// 	"audio": {
			// 		"mandatory": {
			// 			"googEchoCancellation": "false",
			// 			"googAutoGainControl": "false",
			// 			"googNoiseSuppression": "false",
			// 			"googHighpassFilter": "false"
			// 		},
			// 		"optional": []
			// 	},
			// }, gotStream, () => { console.log('Couldn\'t get stream') });
		} catch (e) {
			alert('getUserMedia threw exception 1 :' + e);
		}
	}
	function gotStream(stream) {
		// Create an AudioNode from the stream.
		mediaStreamSource = audioContext.createMediaStreamSource(stream);

		// Create a new volume meter and connect it.
		meter = window.createAudioMeter(audioContext);
		mediaStreamSource.connect(meter);
	}
	// fake reactivity by $score
	function getCrashScreenScoreText()
	{
		let scoreTexts = lang('score_texts')

		if( $score <= 0 )
			return scoreTexts.zero[Math.floor(Math.random() * scoreTexts.zero.length)];
		else if( $level <= 3 )
			return scoreTexts.low[Math.floor(Math.random() * scoreTexts.low.length)];
		else if( $level <= 6 )
			return scoreTexts.high[Math.floor(Math.random() * scoreTexts.high.length)];
		else
			return scoreTexts.master[Math.floor(Math.random() * scoreTexts.master.length)];
	}

	async function drawImg(context, image) {
		return new Promise(resolve => {
			image.onload = function () {
				context.drawImage(image, 0, 0, image.width, $gameHeight);
				resolve(true);
			} 

			if( image.naturalWidth != 0 )
			{
				context.drawImage(image, 0, 0, $gameWidth, $gameHeight+10);
				resolve(true)
			}
		});
	}
</script>

<style>
	:global(body) {
		background: #222;
		color: #EEE;
		user-select: none;
	}
	#canvas-area {
		/*position: relative;*/
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background: #222;
		color: #EEE;
	}

	#playground {
		border: 1px solid #000;
		background: #222;
		color: #EEE;
		margin: 0 auto;
	}
	#start-button {
		font-size: 1.3rem;
		padding: 0.8rem 1.4rem;
		cursor: pointer;
	}
	#sensitivity {
		/*position: absolute;*/
		margin-top: 10px;
		display: flex;
		flex-direction: row;
		white-space: nowrap;
	}
	#sensitivity div {
		margin-right: 1rem;
	}
	#start-screen {
		position: fixed;
		background: rgba(000, 000, 000, 0.7);
		color: #FFF;
		width: 100%;
		height: 100%;
		top: 0; bottom: 0;
		left: 0; right: 0;
		text-align: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	#start-screen .emoji {
		font-size: calc(1rem + 5vw);
		font-weight: bold;
		margin-bottom: 0.8rem;
	}
	#start-screen .emoji-2 {
		font-size: 2rem;
		color: yellow;
		font-weight: normal;
		margin-bottom: 0.8rem;
	}
	#start-screen .message {
		font-size: 1.2rem;
		font-weight: normal;
		font-style: italic;
		margin-top: 3rem;
		margin-bottom: 1rem;
	}
	#crash-screen {
		position: fixed;
		background: rgba(000, 000, 000, 0.7);
		color: #FFF;
		width: 100%;
		height: 100%;
		top: 0; bottom: 0;
		left: 0; right: 0;
		text-align: center;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		display: none;
	}
	#crash-screen .message {
		font-size: 1.2rem;
		font-weight: normal;
		font-style: italic;
		margin-bottom: 0.8rem;
	}
	#crash-screen .score {
		font-size: 2rem;
		font-weight: bold;
	}
	#crash-screen #restart-button {
		font-size: 1.5rem;
		margin-top: 2rem;
		font-weight: bold;
		padding: 0.8rem 1.4rem;
		cursor: pointer;
	}
	
	h1#title {
		margin-bottom: 0;
	}
	#link-area {
		font-size: 0.8rem; text-align: center; font-style: italic; margin-top: 5px; margin-bottom: 10px
	}
	#link {
		font-style:normal;font-weight:bold; color: #eee; text-decoration: none !important
	}
	#ma-text {
		background-color:#D32F2F; color:#FFF;padding:0 4px
	}


	.slider {
		-webkit-appearance: none;
		width: 100%;
		height: 25px;
		background: #d3d3d3;
		outline: none;
		opacity: 0.7;
		-webkit-transition: .2s;
		transition: opacity .2s;
	}

	.slider:hover {
		opacity: 1;
	}

	.slider::-webkit-slider-thumb {
		-webkit-appearance: none;
		appearance: none;
		width: 25px;
		height: 25px;
		background: #4CAF50;
		cursor: pointer;
	}

	.slider::-moz-range-thumb {
		width: 25px;
		height: 25px;
		background: #4CAF50;
		cursor: pointer;
	}


	.lang-changers {
		margin-top: 1.4rem;
	}

	.lang-changers .lang-changer {
		color: #EEE;
		font-size: 1.1rem;
		margin: 0 0.4rem;
	}


	.lang-changers-2 {
		margin-left: 1.4rem;
	}

	.lang-changers-2 .lang-changer {
		color: #EEE;
		font-size: 1.1rem;
		margin: 0 0.4rem;
	}

	#header {
		/*display: flex;*/
		/*align-items: center;*/
		position: relative;
	}
	#main-header {
		/*flex-grow: 1;*/
		/*justify-content: center;*/
		text-align: center
	}
	#right-header {
		position: absolute;
		right: 0;
		top: 50%;
		/*display: flex;*/
		/*justify-content: flex-end;*/
		/*align-content: flex-end;*/
		/*align-items: flex-end;*/
	}
</style>
<svelte:head>
	<title>FlappyDirt üí© v0.907 - MALab [muratalabacak.com]</title>
</svelte:head>
<svelte:window on:resize={handleDomResize} on:load={handleDomReady}/>

<div id="canvas-area">
	
	<div id="header" style="width: {$gameWidth}px">
		<div id="main-header">
			<h1 id="title">FlappyDirt</h1>
			<div id="link-area">
				{lang('product_of_before')}
					<a href="http://muratalabacak.com" target="_blank" id="link"><span id="ma-text">MA</span>Lab</a>
				{lang('product_of_after')}
			</div>
		</div>
		<div id="right-header">
			<div class="lang-changers-2">
				{#if $gameLang == 'tr'}
					<a href="#lang:en" class="lang-changer lang-changer-en" on:click={() => { setGameLang('en') }}>
						English
					</a>
				{:else}
					<a href="#lang:tr" class="lang-changer lang-changer-tr" on:click={() => { setGameLang('tr') }}>
						T√ºrk√ße
					</a>
				{/if}
			</div>
		</div>
	</div>



	<canvas
		id="playground"
		bind:this={canvas}
		width={width}
		height={height}
	/>
	<div id="start-screen" bind:this={startScreen}>
		<div class="emoji">
			{lang('are_you_ready')}
		</div>
		<div class="emoji-2">
			{lang('ready_to_warn')}
		</div>
		<div class="message">
			<strong>{lang('tip')}:</strong> {lang('tip_for_phone')} 
		</div>
		<button id="start-button" on:click={startGame} bind:this={startButton}>
			{lang('start_button')}
		</button>
		<div class="lang-changers">
			{#if $gameLang == 'tr'}
				<a href="#lang:en" class="lang-changer lang-changer-en" on:click={() => { setGameLang('en') }}>
					English
				</a>
			{:else}
				<a href="#lang:tr" class="lang-changer lang-changer-tr" on:click={() => { setGameLang('tr') }}>
					T√ºrk√ße
				</a>
			{/if}
		</div>
	</div>
	<div id="sensitivity" style="bottom: -50px">
		<div>{lang('sensitivity', {'sensitivity': sensitivity})}</div>
		<input min="-100" max="100" type="range" class="slider" on:input={handleSensitivity} bind:value={sensitivity}>
	</div>

	<div id="crash-screen" bind:this={crashScreen}>
		<div class="message">
			{crashScreenScoreText}
		</div>
		<div class="score">
			{lang('score_result', {'score': $score})}
		</div>

		<button id="restart-button" on:click={restartGame} bind:this={restartButton}>
			{lang('restart_button')}
		</button>
		{#if canShare()}
			<button on:click={() => { shareMyScore($score) }}>
				{lang('share_button')}
			</button>
		{/if}


		<div class="lang-changers">
			{#if $gameLang == 'tr'}
				<a href="#lang:en" class="lang-changer lang-changer-en" on:click={() => { setGameLang('en') }}>
					English
				</a>
			{:else}
				<a href="#lang:tr" class="lang-changer lang-changer-tr" on:click={() => { setGameLang('tr') }}>
					T√ºrk√ße
				</a>
			{/if}
		</div>
	</div>
</div>